import Helper from './_helper';

const options = {
    "type": "selectbox", "id": "-", "pri_id": 0, "field_id" : 0 , "seq_no": 0 , "title": "Dropdown List",
    "settings": [
        {"id": 0, "type": "text", "key": "text_title", "values": "Dropdown", "label": "หัวข้อ"},
        {"id": 0, "type": "select", "key": "selectfield", "values": "1", "label": "Field"}
    ],
    "css": [
        {"id": 0, "type": "colorpicker", "key": "color", "values": "#595959", "label": "สีตัวอักษร"},
        {"id": 0, "type": "select", "key": "font-size", "values": "14px", "label": "ขนาดตัวอักษร"}
    ],
    "parent_css": [
        {"id": 0, "type": "select", "key": "width", "values": "100%", "label": "ขนาด"},
        {"id": 0, "type": "select", "key": "padding", "values": "10px", "label": "ระยะห่างของขอบกับวัตถุ"}
    ],
    "label_css": [
        {"id": 0, "type": "colorpicker", "key": "color", "values": "#595959", "label": "สีตัวอักษรของ Label"},
        {"id": 0, "type": "select", "key": "font-size", "values": "14px", "label": "ขนาดตัวอักษรของ Label"},
        {"id": 0, "type": "select", "key": "text-align", "values": "left", "label": "การชิดตัวอักษรของ Label"},
        {"id": 0, "type": "select", "key": "font-weight", "values": "normal", "label": "ความหนาของตัวอักษรของ Label"}
    ],
    "data_set" : []
}

export default {
    push_object(_new_id,_data) {
        options.id = _new_id;
        options.title = _data.name;
        options.settings[0].values = _data.field_name;
        options.field_id = _data.id;
        let new_options = [];
        _data.field_items.map((v,i)=>{
            new_options.push({
                id : v.id , 
                title : v.value
            });
        });
        options.data_set = new_options;
        return options;
    },
    select_template(_options) {
        console.log(["selectbox",_options]);
        let {
            _css,
            _parent_css,
            _label_css
        } = Helper.convertCss(_options);
        let str_builder = "";
        let _arr_data_set = [];
        _options.data_set.map((v,i)=>{
            if(i==0){
                _arr_data_set.push({
                    id : -1 ,
                    title : "-- Select --"
                });
            }
            _arr_data_set.push(v);
        });
        
        _arr_data_set.map((v,i)=>{
            let _xselected = "";
            if(_options.auto_fill!=""){
                if(v.id.toString()===_options.auto_fill){
                    _xselected = `selected="selected"`;
                }
            }else{
                if(i==0)_xselected = `selected="selected"`;
            }
            str_builder += `<option value="${v.title}" ${_xselected}>${v.title}</option>`;
        });
        console.log([
            _arr_data_set,
            str_builder
        ]);
        let _is_required = "" , _is_required2 = "";
        if(_options.field.is_required==1) {
            _is_required = "valid_selectbox";
            _is_required2 = ` <span style="color:#e27826">*</span>`;
        }
        let _is_readonly = "";
        if(_options.field.is_readonly==1 && _options.auto_fill != ""){
            _is_readonly = `disabled`;
        }
        return `
    <div style='float:left;${_parent_css}'>
        <label style='${_label_css}'>${_options.settings[0].values}${_is_required2}</label>
        <select id='${_options.id}' name="field_items[${_options.field_id}]" ${_is_readonly} class='form-control ${_is_required}' style='${_css}'>${str_builder}</select>
    </div>`;
    }
}


